/** @format */

.Facebook {
  width: 100%;
  height: 100vh;
  font-family: Roboto;
}

.Form_Header {
  padding-top: 42px;
  display: grid;
  grid-template-columns: 15% 80%;
}

.Form_Header .Back_Btn {
  margin-top: 5px;
  align-self: baseline;
  text-align: center;
}

.Header_Text h1 {
  text-align: start;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.17;
  letter-spacing: normal;
  color: #3b414a;
}

.Header_Text p:nth-child(2) {
  margin-top: 10px;
  opacity: 0.7;
  font-family: Roboto;
  font-size: 14px;
  line-height: 1.43;
  color: #24272c;
}

.Header_Text p:nth-child(3) {
  font-size: 14px;
  font-weight: 500;
  color: #656f7a;
  margin-top: 8px;
  text-decoration: underline;
}

.ImgContainer {
  width: 100%;
  height: 100%;
  margin-bottom: 10px;
}

.ImgContainer img {
  width: 40%;
  margin: 8% auto auto auto;
  display: block;
}

.ImgContainer h3 {
  margin-top: 20px;
  text-transform: capitalize;
  opacity: 0.5;
  font-size: 15px;
  line-height: 1.07;
  text-align: center;
  color: #24272c;
}

.Social_Icons {
  width: 100%;
}

.Social_Icons img {
  margin-left: 15%;
  display: block;
  width: 65%;
}

.Social_Btn {
  width: 70%;
  margin: 30px auto;
  font-size: 20px;
  font-weight: 500;
  color: #434a54;
}

.Social_Btn h1 {
  font-family: Roboto;
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 20px;
  color: #434a54;
}

.Social_Btn button {
  display: flex;
  background-color: #4267b2;
  align-items: center;
  border-radius: 2px;
  font-size: 15px;
  padding: 7px 20px 7px 13px;
  margin: 20px 0px 0px 0;
  letter-spacing: 2px;
}

.Social_Btn img {
  width: 25px;
  margin-right: 10px;
}
